@use "vars";
.mobile-menu {
  padding: 24px;
  background-color: var(--white);
  position: absolute;
  top: 60px;
  left: 0;
  border: 2px solid var(--hospital-green);
  border-radius: 10px;
}
.mobile-menu a {
  text-decoration: none;
  color: var(--black);
  font-weight: bold;
  /* margin-bottom: 24px; */
}
.mobile-menu ul {
  padding: 0;
  margin: 24px 0 0;
  list-style: none;
}
.mobile-menu ul:nth-child(1) {
  border-bottom: 1px solid var(--very-light-pink);
}
.mobile-menu ul li {
  margin-bottom: 24px;
}
.email {
  font-size: var(--sm);
  font-weight: 300 !important;
}
.create-account,
.login {
  font-size: var(--sm);
  color: var(--hospital-green) !important;
}
  